import React,{Component} from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { apiRoute, getApiHeader } from '../../utils/helpers';
import {setLoadingTrue, clearAllAlerts} from '../../actions/userAction';
import { imagePath } from '../../utils/helpers';
import Header from '../../components/frontend/common/Header';
import Footer from '../../components/frontend/common/Footer';
import Services from '../../components/frontend/common/Services';
import FooterTopSec from '../../components/frontend/common/FooterTopSec';
import HomeBanner from '../../components/frontend/home/HomeBanner';
import AfterIntentionSection from '../../components/frontend/home/AfterIntentionSection';
import AfterBannnerSection from '../../components/frontend/home/AfterBannnerSection';
import Intentions from '../../components/frontend/home/Intentions';
import Testimonials from '../../components/frontend/home/Testimonials';
import Benefits from '../../components/frontend/home/Benefits';
import Practices from '../../components/frontend/home/Practices';
import RecentPost from '../../components/frontend/home/RecentPost';

class Index extends Component{

	constructor(props){
    	super(props);
	      this.state = {
	      	banner : [],
	      	sections:[
	      	[],[],[],[],[],[],[]
	      	],
		    } 
		  }

	changeTestimonial = (video) => {
		this.setState({testimonialVideo: video});
	}

	resetTestimonialVideo = () => {
		this.setState({testimonialVideo: ''});
	}

	componentWillMount() {
		
    	window.scrollTo(0, 0);

    	this.props.setLoadingTrue();

    	const requestOptions = {
			headers : getApiHeader()
		};

		axios.get(apiRoute('cms-page-banner/'+btoa(1)),requestOptions)
		.then(res =>{
			this.setState({ banner : res.data });
	    })

		axios.get(apiRoute('cms-page-data/'+btoa(1)),requestOptions)
		.then(res =>{
			this.setState({ sections : res.data });
			this.props.clearAllAlerts();
	    })
	}
	
	

	render(){

		const getBackgroundImage = (image) => {
		var img = {
				backgroundImage: `url(${image})`,
				backgroundRepeat: `no-repeat`,
				backgroundPosition: `center`,
				}
				return img;
			}
		return(
			<>
			<Header/>
			<HomeBanner banner={this.state.banner} getImage={getBackgroundImage}/>
			<main>
			<AfterBannnerSection sections={this.state.sections} getImage={getBackgroundImage}/>
			<Practices/>
			<Intentions getImage={getBackgroundImage}/>
			<Benefits/>

			 <section className="sec sec-classes" style={getBackgroundImage(imagePath('cms/'+this.state.sections['2'].image))}>
		      <div className="container">
		        <div className="row align-items-center">
		          <div className="col-lg-6 col-md-6 col-sm-12">
		            <h2>{this.state.sections['2'].title} </h2>
		            <h5>{this.state.sections['2'].description}</h5>
		            <Link to={this.state.sections['2'].link} className="waves-effect waves-light btn btn-lg">See More classes</Link>
		          </div>
		        </div>
		      </div>
		    </section>

			<Testimonials changeTestimonialLink={this.changeTestimonial}/>

			<AfterIntentionSection sections={this.state.sections} getImage={getBackgroundImage}/>

			<RecentPost/>

			 <section className="sec sec-class"  style={getBackgroundImage(imagePath('cms/'+this.state.sections['6'].image))}>
		      <div className="container">
		        <div className="row">
		          <div className="col-lg-6 col-md-6 text-white">
		              <h2>{this.state.sections['6'].title}.</h2>
		              <h5>{this.state.sections['6'].description}</h5>
		              <div className="device-icon"><img src="#" alt=""/></div>
		              <a href={this.state.sections['6'].link} className="btn btn-lg">Sign Up Now</a>
		          </div>
		          <img className="devices" src={require('../../assets/images/bg-laptop.png')} alt=""/>
		        </div>
		      </div>
		    </section>

			<Services getImage={getBackgroundImage} page={1}/>

			<FooterTopSec/>

			</main>
			<Footer/>

			<div className="modal fade" id="testimonial" tabindex="-1" role="dialog" aria-labelledby="testimonial2Title" aria-hidden="true">
		    <div className="modal-dialog modal-lg modal-dialog-centered" role="document">
		      <div className="modal-content">
		      <div className="modal-header">
		        <button type="button" className="close" data-dismiss="modal" onClick={this.resetTestimonialVideo}>&times;</button>
		      </div>
		        <div className="modal-body">
		          <iframe className="ifmplayer" src={this.state.testimonialVideo} frameborder="0" width="100%" height="480"></iframe>
		        </div>
		      </div>
		    </div>
		  </div>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});

export default connect(mapStateToProps, {setLoadingTrue, clearAllAlerts} )(Index);