import React,{Component} from 'react';
import { Link } from 'react-router-dom';
import { imagePath } from '../../../utils/helpers';

function AfterBannnerSection({sections, getImage}){
		
		return(
			<>
			 <section className="sec sec-fvideo text-center">
			      <div className="col xl12">
			        <iframe className="responsive-video" src={sections['0'].link} frameBorder="0" width="853" height="480" allowFullScreen></iframe>
			     </div>
			     <h2>Living Enlightenment</h2>
			</section>
			<section className=""  style={getImage(imagePath('cms/'+sections['1'].image))}>
			      <div className="container sec">
			        <div className="row">
			          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12"></div>
			          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 about-text">
			               <h4>{sections['1'].title}</h4>
				            <div dangerouslySetInnerHTML={{__html: sections['1'].description}}>
						 	</div>
			              <ul>
			                <li><a target="_blank" href="https://www.facebook.com/sattvaconnect/"><i className="fab fa-facebook-f"></i></a></li>
			                <li><a target="_blank" href="https://www.instagram.com/sattvaconnect/"><i className="fab fa-instagram"></i></a></li>
			              </ul>
			              <Link to={sections['1'].link} className="btn btn-lg">Learn More</Link>
			          </div>
			        </div>
			     </div>
   			</section>
   			 </>
			);
}

export default AfterBannnerSection;