import React from 'react';
import RoadToDharmaHeader from '../../components/frontend/common/RoadToDharmaHeader';
import Footer from '../../components/frontend/common/Footer';
import RegistrationForm from '../../components/frontend/auth/RegistrationForm';

export default function UserRegistration() {
    return (
        <div>
            <RoadToDharmaHeader />
            <main>
                <section className="sec-cinfo">
                    <div className="container">
                        
                        {alert.message && alert.type === 'error' &&
                            <div className="alert alert-danger" role="alert">
                                {alert.message}
                            </div>}
                        {alert.message && alert.type === 'success' &&
                            <div className="alert alert-success" role="alert">
                                {alert.message}
                            </div>}
                        <div className="card subscription-card customer-support">
                        <h3>User Registration</h3>
                            <RegistrationForm />
                        </div>
                    </div>
                </section>
            </main>
            <Footer />
        </div>
    )
}
