import React,{Component} from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import OwlCarousel from 'react-owl-carousel';
import { apiRoute, getApiHeader } from '../../../utils/helpers';

class Intentions extends Component{

	constructor(props){
    	super(props);
	      this.state = {
	        intentions:[],
		    } 
		  }

	componentWillMount() {
		
    window.scrollTo(0, 0);

    const requestOptions = {
			headers : getApiHeader()
		};
		axios.get(apiRoute('cms-all-intentions'),requestOptions)
		.then(res =>{
			this.setState({ intentions : res.data });
	    })
	}
	
	render(){

		return(
		<>
		 {this.state.intentions && this.state.intentions.length !== 0 ? 
		  <section>
		    <OwlCarousel className="owl-carousel owl-theme owlslider-block" nav={true} items={1}>
		       {this.state.intentions.map((item,index)=>{
		       	if (index === 0) {
				    return(	
				        <div className="connectslider-wrapper" style={this.props.getImage(item.image)} key={index}>
				            <div className="container">
				              <div className="row">
				                <div className="col-lx-12 col-lg-12 col-md-12 col-sm-12 text-center">
				                    <h4>{item.title}</h4>
				                    <h1>{item.description}</h1>
				                </div>
				              </div>
				            </div>
				        </div>
			        	);
			    	}else{
			    	return(	
				        <div className="connectslider-wrapper" style={this.props.getImage(item.image)} key={index}>
				            <div className="container">
					            <div className="row">
					             <div className="col-lx-6 col-lg-6 col-md-12 col-sm-12"></div>
					             <div className="col-lx-6 col-lg-6 col-md-12 col-sm-12 contact-content">
					                <h2>{item.title}</h2>
					                <p>{item.description} </p>
					                {item.link && item.link !== '' ? 
					                <a class="btn btn-lg waves-effect waves-light" href={item.link}>Signup</a>
					              : ''}
					             </div>
					           </div>
					          </div>
				        </div>
			        	);
			    		}
				})}	
		    </OwlCarousel>
		    </section>
		      : ''}
		      </>
		);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {})(Intentions);