import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import { apiRoute, getApiHeader } from '../../../utils/helpers';
import { setLoadingTrue, clearAllAlerts } from '../../../actions/userAction';
import CourseServices from '../../../services/courseServices';
import SimpleReactValidator from 'simple-react-validator';

class QuickRegistrationForm extends Component {

    constructor(props) {
        super(props);
        this.validator = new SimpleReactValidator();
        this.state = {
            firstName: '',
            lastName: '',
            email: '',
            address: '',
            phone: '',
            affiliateUrlId: this.props.affiliateUrlId,
            courseId: this.props.courseId,
            emailAvailability: true,
            error:false,
            errorMessages:'',
            redirect:false,
        }
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onEmailChange = this.onEmailChange.bind(this);
    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onEmailChange(e) {
        const email = e.target.value;
        this.setState({ email: email });
        if(typeof email !== "undefined"){
            let lastAtPos = email.lastIndexOf('@');
            let lastDotPos = email.lastIndexOf('.');
 
            if ((lastAtPos < lastDotPos && lastAtPos > 0 && email.indexOf('@@') == -1 && lastDotPos > 2 && (email.length - lastDotPos) > 2)) {
                CourseServices.checkEmailAvailability(email)
                .then(res => {
                    if (res.data.status !== true) {
                        this.setState({ emailAvailability: false });
                    } else {
                        this.setState({ emailAvailability: true });
                    }
                })
             }else{
                this.setState({ emailAvailability: true });
             }
        }else{
            this.setState({ emailAvailability: true });
        } 
    }

    onSubmit(e) {
        e.preventDefault();
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            this.forceUpdate();
            return false;
        }

        if (this.state.emailAvailability === false) {
            return false;
        }

        const userData = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            phone: this.state.phone,
            courseId: this.state.courseId,
            affiliateUrlId: this.state.affiliateUrlId,
        }
        this.props.setLoadingTrue();
        const requestOptions = {
            headers: getApiHeader()
        };
        axios.post(apiRoute('new-course-user-registration'), userData, requestOptions)
            .then(res => {
                this.setState({
                    redirectToPayment: true,
                    error:false,
                    errorMessages: '',
                    redirect:true,
                })
                this.props.clearAllAlerts();
            })
            .catch(error => {
                if (error.response && error.response.status) {
                    let errorData = error.response.data;
                    this.setState({
                        error:true,
                        errorMessages: errorData.message,
                    })
                } else {
                    this.setState({
                        error:true,
                        errorMessages: 'Something Went Wrong, Try Again',
                    })
                }
                this.props.clearAllAlerts();
            });

    }

    render() {
        return (
            <>
            {this.state.redirect &&
                <Redirect to={window.PUBLIC_URL+'/registration_success'}/>
            }
                        <form onSubmit={this.onSubmit} id='buyCourseForm' autoComplete="off" className="reg-form">
                            <h4>Your Details</h4>
                            {this.state.error ?
                                <div className="col-sm-6">
                                    <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                        <p>{this.state.errorMessages}.</p>
                                    </div></div> :null}
                            {this.state.emailAvailability === false &&
                                <div className="col-sm-6">
                                    <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                        <p>Email is already in used.</p>
                                    </div></div>}
                            <div className="row">
                                <div className="col-md-6">
                                <div className="form-group">
                                    <input type="text" name="firstName" placeholder="First Name" className="form-control" id="firstName" value={this.state.firstName} onChange={this.onChange} />
                                    {this.validator.message('firstName', this.state.firstName, 'required|alpha|max:50')}
                                </div>
                                </div>
                                <div className="col-md-6">
                                <div className="form-group">
                                    <input type="text" name="lastName"  placeholder="Last Name" className="form-control" id="lastName" value={this.state.lastName} onChange={this.onChange} />
                                    {this.validator.message('lastName', this.state.lastName, 'required|alpha|max:50')}
                                </div>
                                </div>
                                <div className="col-md-6">
                                <div className="form-group">
                                    <input type="text" name="email" placeholder="Email" className="form-control" id="email" value={this.state.email} onChange={this.onEmailChange} />
                                    {this.validator.message('email', this.state.email, 'required|email')}
                                </div>
                                </div>
                                <div className="col-md-6 ">
                                <div className="form-group">
                                    <input type="number" name="phone" placeholder="Phone" className="form-control" id="phone" value={this.state.phone} onChange={this.onChange} />
                                    {this.validator.message('phone', this.state.phone, 'required|min:10|phone')}
                                </div>
                                </div>
                            </div>
                            <div className="btn-div col-md-12">
                                <button type="submit" className="btn btn-black">Submit</button>
                            </div>
                        </form>
            </>
        );
    }
}

const mapStateToProps = (state) => ({
    course: state.courses.item,
});
export default connect(mapStateToProps, { setLoadingTrue, clearAllAlerts })(QuickRegistrationForm);