import React,{Component} from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { apiRoute, getApiHeader } from '../../utils/helpers';
import {setLoadingTrue, clearAllAlerts} from '../../actions/userAction';
import Header from '../../components/frontend/common/Header';
import Footer from '../../components/frontend/common/Footer';

class PrivacyPolicy extends Component{

	constructor(props){
    	super(props);
	      this.state = {
	      	pageData : [
	      	[]
	      	],
		    } 
		  }

	componentWillMount() {
		
    window.scrollTo(0, 0);

    this.props.setLoadingTrue();

   	 const requestOptions = {
			headers : getApiHeader()
		};
		axios.get(apiRoute('cms-page-data/'+btoa(4)),requestOptions)
		.then(res =>{
			this.setState({ pageData : res.data });
			this.props.clearAllAlerts();
	    })
	}

	render(){
		return(
			<>
			<Header/>
			 <main> 
			  <section className="sec sec-inabout">
			      <div className="container" dangerouslySetInnerHTML={{__html: this.state.pageData['0'].description}}>
			     </div>
			    </section>  
			 </main>
			<Footer/>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {setLoadingTrue, clearAllAlerts})(PrivacyPolicy);