import React, { Component } from 'react';
import axios from 'axios';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import DropIn from "braintree-web-drop-in-react";
import { getApiHeader } from '../../../utils/helpers';
import { getClintId } from '../../../actions/courseAction';
import SimpleReactValidator from 'simple-react-validator';
import CountryOptions from '../common/CountryOptions';
import CourseServices from '../../../services/courseServices';
import PhoneCodeOptions from '../common/PhoneCodeOptions';

const initialState = {
    username: '',
    password: '',
    confirmPassword: '',
    firstName: '',
    lastName: '',
    address1: '',
    address2: '',
    city: '',
    zip: '',
    country: '',
    countryCode: '',
    phone: '',
    email: '',
    plans: [],
    plan: '',
    labelClass: '',
    couponCode:'',
    emailAvailability: true,
    usenameAvailability: true,
    successMessage: false,
};

class RegistrationForm extends Component {
    constructor(props) {
        super(props);
        this.validator = new SimpleReactValidator();
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onCountryChange = this.onCountryChange.bind(this);
        this.onEmailChange = this.onEmailChange.bind(this);
        this.onUsernameChange = this.onUsernameChange.bind(this);
        this.state = initialState;
    }
    componentDidMount() {
        this.props.getClintId(this.props.history);
        const requestOptions = {
            headers: getApiHeader()
        };

        axios.get('https://betasite.online/sattva-courses/api/get-all-plans', requestOptions)
            .then(res => {
                this.setState({ plans: res.data });
            })
    }
    onCountryChange(e) {
        var index = e.target.selectedIndex;
        var optionElement = e.target.childNodes[index]
        var phoneCode = optionElement.getAttribute('phoneCode');
        this.setState({ country: e.target.value });
        this.setState({ countryCode: phoneCode });
    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onEmailChange(e) {
        const email = e.target.value;
        this.setState({ email: email });
        if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
            CourseServices.checkUserEmail(email)
                .then(res => {
                    if (res.data.status == true) {
                        this.setState({ emailAvailability: true });
                    } else {
                        this.setState({ emailAvailability: false });
                    }
                })
        } else {
            return false;
        }
    }

    onUsernameChange(e) {
        const username = e.target.value;
        this.setState({ username: username });
        CourseServices.checkUserUsername(username)
            .then(res => {
                if (res.data.status == true) {
                    this.setState({ usenameAvailability: true });
                } else {
                    this.setState({ usenameAvailability: false });
                }
            })

    }

    async onSubmit(e) {
        e.preventDefault();

        if (!this.validator.allValid()) {
            this.validator.showMessages();
            this.forceUpdate();
            return false;
        }
        const { nonce } = await this.instance.requestPaymentMethod();

        const userDetail = {
            username: this.state.username,
            password: this.state.password,
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            address1: this.state.address1,
            address2: this.state.address2,
            city: this.state.city,
            zip: this.state.zip,
            country: this.state.country,
            countryCode: this.state.countryCode,
            phone: this.state.phone,
            email: this.state.email,
            plan: this.state.plan,
            couponCode:this.state.couponCode,
            paymentDetails: nonce,
        }

        CourseServices.userRegistration(userDetail)
            .then(res => {
                window.scrollTo(0, 0);
                this.setState(initialState);
                this.setState({ successMessage: true });
            })
    }

    render() {
        const clintId = this.props.braintree;
        return (
            <>
                <form onSubmit={this.onSubmit} id='giftCourseForm' autocomplete="off" className="form form-horizontal">

                    <div className="card p-4 d-block">
                        <div className="customer-support p-0">
                            {this.state.successMessage == true &&
                               <Redirect to="/user-registration-success" />
                            }
                            {this.state.emailAvailability == false &&
                                <div className="col-sm-6">
                                    <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                        <p>Email is already in used.</p>
                                    </div>
                                </div>
                            }
                            {this.state.usenameAvailability == false &&
                                <div className="col-sm-6">
                                    <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                        <p>Username is already in used.</p>
                                    </div>
                                </div>
                            }
                            <div className="row detail-form">
                                <div className="input-field col-md-6">
                                    <label id="firstName-lbl" for="firstName" className=" required">First Name<span className="star">&#160;*</span></label>
                                    <input type="text" name="firstName" id="firstName" value={this.state.firstName} onChange={this.onChange} />
                                    {this.validator.message('firstName', this.state.firstName, 'required|alpha|max:50')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="lastName-lbl" for="lastName" className="required">Last Name<span className="star">&#160;*</span></label>
                                    <input type="text" name="lastName" id="lastName" value={this.state.lastName} onChange={this.onChange} />
                                    {this.validator.message('lastName', this.state.lastName, 'required|alpha|max:50')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="email-lbl" for="email" className=" required">Email<span className="star">&#160;*</span></label>
                                    <input type="text" name="email" id="email" value={this.state.email} onChange={this.onEmailChange} />
                                    {this.validator.message('email', this.state.email, 'required|email')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="username-lbl" for="username" className=" required">Username<span className="star">&#160;*</span></label>
                                    <input type="text" name="username" id="username" autoComplete="new-username" value={this.state.username} onChange={this.onUsernameChange} />
                                    {this.validator.message('username', this.state.username, 'required')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="password-lbl" for="password" className=" required">Password<span className="star">&#160;*</span></label>
                                    <input type="password" name="password" id="password" autoComplete="new-password" value={this.state.password} onChange={this.onChange} />
                                    {this.validator.message('password', this.state.password, 'required')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="confirmPassword-lbl" for="confirmPassword" className=" required">Retype-password<span className="star">&#160;*</span></label>
                                    <input type="password" name="confirmPassword" id="confirmPassword" value={this.state.confirmPassword} onChange={this.onChange} />
                                    {this.validator.message('confirm password', this.state.confirmPassword, 'required')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="address1-lbl" for="address1" className="required">Address1<span className="star">&#160;*</span></label>
                                    <input type="text" name="address1" id="address1" value={this.state.address1} onChange={this.onChange} />
                                    {this.validator.message('address1', this.state.address1, 'required|alpha')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="address2-lbl" for="address2" className="required">Address2</label>
                                    <input type="text" name="address2" id="address2" value={this.state.address2} onChange={this.onChange} />
                                    {this.validator.message('address2', this.state.address2, 'alpha')}
                                </div>
                                <div className="input-field col-md-6">
                                    <select id="country" name="country" onChange={this.onCountryChange}>
                                        <option selected>Receiver's Country *</option>
                                        <CountryOptions />
                                    </select>
                                    {this.validator.message('country', this.state.country, 'required')}
                                </div>
                                <div className="input-field col-md-6">
                                    <select id="countryCode" name="countryCode" value={this.state.countryCode} onChange={this.onChange}>
                                        <option>Receiver's Dial Code(+) *</option>
                                        <PhoneCodeOptions />
                                    </select>
                                    {this.validator.message('countryCode', this.state.countryCode, 'required')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="city-lbl" for="city" className="required">City<span className="star">&#160;*</span></label>
                                    <input type="text" name="city" id="city" value={this.state.city} onChange={this.onChange} />
                                    {this.validator.message('city', this.state.city, 'required')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="zip-lbl" for="zip" className="required">Zip Code<span className="star">&#160;*</span></label>
                                    <input type="text" name="zip" id="zip" value={this.state.zip} onChange={this.onChange} />
                                    {this.validator.message('zip', this.state.zip, 'required|min:6|integer')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="phone-lbl" for="phone" className="required">Phone<span className="star">&#160;*</span></label>
                                    <input type="text" name="phone" id="phone" value={this.state.phone} onChange={this.onChange} />
                                    {this.validator.message('phone', this.state.phone, 'required|min:10|phone')}
                                </div>
                                <div className="input-field col-md-6">
                                    <select id="plan" name="plan" value={this.state.plan} onChange={this.onChange}>
                                        <option>Select plan *</option>
                                        {this.state.plans.map((item, index) => {
                                            return (
                                                <option key={index} value={item.id}>{item.name}</option>
                                            );
                                        })}
                                    </select>
                                    {this.validator.message('plan', this.state.plan, 'required')}
                                </div>
                                <div className="input-field col-md-6">
                                    <label id="couponCode-lbl" for="couponCode" className="required">Coupon Code</label>
                                    <input type="text" name="couponCode" id="couponCode" value={this.state.couponCode} onChange={this.onChange} />
                                </div>
                                <div className="col-md-12">
                                    {clintId ? (
                                        <DropIn
                                            options={{ authorization: this.props.braintree }}
                                            onInstance={instance => (this.instance = instance)}
                                        />
                                    )
                                        : ''
                                    }
                                </div>

                            </div>
                            <div className="input-field col-md-6 text-right">
                                <button type="submit" className="btn btn-lg">Register</button>
                            </div>
                        </div>
                    </div>
                </form>
            </>
        );
    }
}

const mapStateToProps = (state) => ({
    alert: state.alert,
    braintree: state.courses.braintree,
});


export default connect(mapStateToProps, { getClintId })(RegistrationForm);